.wallpaper1 {
    width: 100%;
    background-image: url(../../build/img/city.webp);
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;  
    overflow: scroll;
    overflow-x: hidden;
}

.main1 {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    

    .contenedor {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 0 auto;
        align-items: center;
    }

    .paso {
        width: 80%;
        height: auto;
        background-color: rgba(0, 0, 0, 0.567) ;

        h3 {
            color: $blanco;
        }

        p {
            text-align: justify;
            padding: 1rem;
        }

        div {
            margin: 0 auto;
            width: 10rem;
        }
    }
}