body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  width: 100%;
}

p {
  margin: 0;
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #2b364a;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

.navegacion {
  width: 100%;
  height: auto;
  margin: 0 auto;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 0;
}
.navegacion .logo {
  width: 100%;
  height: 7rem;
  display: flex;
  flex-direction: row;
  align-items: end;
  justify-content: center;
}
@media (min-width: 768px) {
  .navegacion .logo {
    width: 100%;
  }
}
.navegacion .logo img:hover {
  cursor: pointer;
}
.navegacion .logo .np {
  background-size: contain;
  height: 100%;
}
.navegacion .logo .bandera {
  height: 3rem;
  margin-bottom: 0.5rem;
}
.navegacion .logo .tLogo {
  color: #FFFFFF;
  width: auto;
  font-size: 2rem;
  text-align: center;
  margin: 0 0 0 1rem;
  background: linear-gradient(45deg, #f2c830 0%, #f2c830 10%, #294ddb 49%, #e0232d 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.navegacion .logo .fa-bars {
  font-size: 5rem;
  margin: 1rem;
}
.navegacion .logo .fa-bars:hover {
  cursor: pointer;
  color: #e0232d;
}
@media (min-width: 768px) {
  .navegacion .logo .fa-bars {
    position: absolute;
    right: 0;
    display: none;
  }
}
.navegacion .lista {
  width: 100%;
  padding: 0 1rem 0 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  gap: 0.2rem;
}
@media (min-width: 768px) {
  .navegacion .lista {
    display: flex;
    flex-direction: row;
    height: auto;
    gap: 1rem;
  }
}
@media (min-width: 1024px) {
  .navegacion .lista {
    height: auto;
  }
}
.navegacion .lista li {
  display: flex;
  height: 100%;
  gap: 1rem;
  list-style: none;
  text-transform: uppercase;
  height: auto;
  width: auto;
  padding: 0.5rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion .lista li {
    font-size: 1.4rem;
  }
}
@media (min-width: 1024px) {
  .navegacion .lista li {
    font-size: 2.8rem;
  }
}
.navegacion .lista li:hover {
  cursor: pointer;
  border-radius: 0.7rem;
  /* Permalink - use to edit and share this gradient. Enlace permanente - úsalo para editar y compartir este gradiente: https://colorzilla.com/gradient-editor/#f2c830+0,294ddb+49,e0232d+51,7db9e8+100 */
  background: linear-gradient(45deg, #f2c830 0%, #294ddb 49%, #e0232d 51%, #7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.navegacion .desbloquear {
  display: flex;
}
.navegacion .seleccion2 {
  background: linear-gradient(45deg, #f2c830 0%, #294ddb 49%, #e0232d 51%, #7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  border-radius: 0.8rem;
}

.wallpaper3 {
  width: 100%;
  background-image: url(../../build/img/winner.webp);
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
}

.wallpaper2 {
  width: 100%;
  background-image: url(../../build/img/inmobiliario.webp);
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
}

.wallpaper1 {
  width: 100%;
  background-image: url(../../build/img/city.webp);
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: hidden;
}

.main1 {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.main1 .contenedor {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 0 auto;
  align-items: center;
}
.main1 .paso {
  width: 80%;
  height: auto;
  background-color: rgba(0, 0, 0, 0.567);
}
.main1 .paso h3 {
  color: #FFFFFF;
}
.main1 .paso p {
  text-align: justify;
  padding: 1rem;
}
.main1 .paso div {
  margin: 0 auto;
  width: 10rem;
}

.wallpaper0 {
  width: 100%;
  background-image: url(../../build/img/cartagena.webp);
  height: auto;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
}

.main0 {
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: auto;
  width: 95%;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .main0 {
    flex-direction: column;
    width: 85%;
    margin: 1rem auto;
  }
}
@media (min-width: 1024px) {
  .main0 {
    flex-direction: row;
    width: 90%;
    margin: 1rem auto;
    padding: 2rem;
    height: 100vh;
  }
}
.main0 h2 {
  color: #FFFFFF;
  margin: 0;
}

.npCol {
  height: auto;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  margin: 5rem 0 0 2rem;
  justify-content: center;
  border-radius: 0.5rem;
  width: 95%;
  margin: 0 auto;
  padding: 1rem;
}
@media (min-width: 768px) {
  .npCol {
    flex-direction: column;
    width: 80%;
    margin: 1rem auto;
    padding: 2rem;
  }
}
.npCol p {
  text-align: justify;
}

.informacion {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;
}
.informacion .mision-vision {
  margin: 1rem;
  width: 90%;
  height: auto;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 0.5rem;
  padding: 2rem;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .informacion .mision-vision {
    flex-direction: column;
    width: 70%;
    margin: 1rem auto;
  }
}
@media (min-width: 1024px) {
  .informacion .mision-vision {
    height: 25rem;
  }
}
.informacion .mision-vision p {
  text-align: justify;
}
@media (min-width: 1024px) {
  .informacion .mision-vision p {
    font-size: 2.3rem;
  }
}

.footer {
  background-color: #000000;
  color: aliceblue;
  height: 7rem;
  width: 100%;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.footer i {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .footer i {
    font-size: 5.5rem;
  }
}
.footer i:hover {
  cursor: pointer;
}

.bloquear {
  display: none;
}