.wallpaper0 {
    width: 100%;
    background-image: url(../../build/img/cartagena.webp);
    height: auto;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;     

}

.main0 {
    margin-top: 2%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: auto;
    width: 95%;
    margin: 1rem auto;
    
    @include tablet {
        flex-direction: column;
        width: 85%;
        margin: 1rem auto;
    }

    @include desktop {
        flex-direction: row;
        width: 90%;
        margin: 1rem auto;
        padding: 2rem;
        height: 100vh;
    }

    h2 {
        color: $blanco;
        margin: 0;
     }
}

.npCol {
    height: auto;
    width: 100%;
    background-color: rgba($color: #000000, $alpha: .7);
    margin: 5rem 0 0 2rem;
    justify-content: center;
    border-radius: .5rem;
    width: 95%;
    margin: 0 auto;
    padding: 1rem;

    @include tablet {
        flex-direction: column;
        width: 80%;
        margin: 1rem auto;
        padding: 2rem;
    }

    p{
        text-align: justify;
    }
}

.informacion {
 width: 100%;
 height: auto;
 display: flex;
 flex-direction: column;
 align-items: end;
 justify-content: center;


 
 .mision-vision {
    margin: 1rem;
    width: 90%;
    height: auto;
    background-color: rgba($color: #000000, $alpha: .7);
    border-radius: .5rem;
    padding: 2rem;
    margin: 1rem auto;

    @include tablet {
        flex-direction: column;
        width: 70%;
        margin: 1rem auto;
    }

    @include desktop {
        height: 25rem;
    }

    p{
        text-align: justify;

        @include desktop {
            font-size: 2.3rem;
        }
    }

 }
}

.footer {
    background-color: #000000;
    color: aliceblue;
    height: 7rem;
    width: 100%;
    
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    

    i {
        font-size: 3rem;

        @include tablet {
            font-size: 5.5rem;
        }

        &:hover{
            cursor: pointer;
        }
    }
}

.bloquear {
    display: none;
}
