.navegacion {
    width: 100%;
    height: auto;
    margin: 0 auto;
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: sticky;
    top: 0;
   

    .logo {

        width: 100%;
        height: 7rem;
        display: flex;
        flex-direction: row;
        align-items: end;
        justify-content: center;

        @include tablet {
            width: 100%;
        }
       

        img {
            &:hover {
                cursor: pointer;
            }
        }
        .np { 
            background-size: contain;
            height: 100%;  
        }

        .bandera {
            height: 3rem;
            margin-bottom: 0.5rem;    
        }  
        
        .tLogo {
            color: $blanco;
            width: auto;
            font-size: 2rem;
            text-align: center;
            margin: 0 0 0 1rem;
            background: linear-gradient(45deg,  #f2c830 0%,#f2c830 10%,#294ddb 49%,#e0232d 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }

        .fa-bars {
            font-size: 5rem;
            margin: 1rem;
            
            &:hover{
                cursor: pointer;
                color: #e0232d;
            }

            @include tablet{
                position: absolute;
                right: 0;
                display: none;
            }

        }
       
    }

    .lista {
        width: 100%;
        padding: 0 1rem 0 1rem;
        display: none;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;        
        margin: 0;
        gap: 0.2rem;

        @include tablet {
            display: flex;
            flex-direction: row;
            height: auto;
            gap: 1rem;
        }

        @include desktop {
            height: auto;
        }

        li{
            display: flex;            
            height: 100%;
            gap: 1rem;
            list-style: none; 
            text-transform: uppercase;  
            height: auto;
            width: auto;
            padding: .5rem;
            color: $blanco;
            font-weight: $bold;
            font-size: 1.3rem;
            text-align: center;
            @include tablet {
                font-size: 1.4rem;
            }

            @include desktop {
                font-size: 2.8rem;
            }

            &:hover {
                cursor: pointer;
                border-radius: .7rem;
                /* Permalink - use to edit and share this gradient. Enlace permanente - úsalo para editar y compartir este gradiente: https://colorzilla.com/gradient-editor/#f2c830+0,294ddb+49,e0232d+51,7db9e8+100 */
                background: linear-gradient(45deg,  #f2c830 0%,#294ddb 49%,#e0232d 51%,#7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

            }
            
        }

    }
    .desbloquear {
        display: flex;
    }
  

    .seleccion2 {
        background: linear-gradient(45deg,  #f2c830 0%,#294ddb 49%,#e0232d 51%,#7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                border-radius: .8rem;
    }
}

